// dependencies
import { useEffect } from 'react';
import { connect } from 'react-redux';
import { checkPropTypes, func, array } from 'prop-types';
// components
import DoughInventoryTable from './DoughInventoryTable';
import ToppingInventoryTable from './ToppingInventoryTable';
import Button from '../../components/Button';
import Container from './styles';
// utils
import { inventoryActions } from '../../actions';

function InventoryScreen({ onGetInventoryStats, doughInventory, toppingInventory }) {
  useEffect(() => {
    onGetInventoryStats();
  }, []);

  return (
    <Container>
      <div className="btnBox">
        <Button btnText="Back" />
        <Button btnText="View Past Inventory" />
        <Button btnText="Submit" />
      </div>
      <DoughInventoryTable list={doughInventory} />
      <ToppingInventoryTable list={toppingInventory} />
    </Container>
  );
}

InventoryScreen.defaultProps = {
  onGetInventoryStats: checkPropTypes(),
  toppingInventory: {},
  doughInventory: {},
};

InventoryScreen.propTypes = {
  onGetInventoryStats: func,
  toppingInventory: array,
  doughInventory: array,
};

const mapStateToProps = (state) => {
  const { doughInventory, toppingInventory } = state.inventory;

  return ({
    doughInventory,
    toppingInventory,
  });
};

const mapDispatchTOProps = (dispatch) => {
  const { getInventoryStats } = inventoryActions;

  return ({
    onGetInventoryStats: () => getInventoryStats().then((data) => dispatch({ type: 'GET_INVENTORY_STATS', data })),
  });
};

export default connect(mapStateToProps, mapDispatchTOProps)(InventoryScreen);
