import { array } from 'prop-types';
import { v4 } from 'uuid';

import { TableContainer as Container } from './styles';

function ToppingInventoryTable({ list }) {
  return (
    <Container rows={parseInt(list.length, 10) + 1}>
      <div className='tableHeader'>
        <div>Topping</div>
        <div>Calculated Amount</div>
        <div>Actual Amount</div>
        <div>Measurement</div>
        <div>Amount Spent</div>
        <div>Calculated Difference $</div>
        <div>Calculated Difference %</div>
      </div>
      {list?.map((topping) => (
        <div key={v4()}>
          <div>{topping.topping}</div>
          <div>{topping.amount}</div>
          <div><input type="text" /></div>
          <div>{topping.measurement}</div>
          <div>${topping.amountSpent}</div>
          <div>$0</div>
          <div>0%</div>
        </div>
      ))}
    </Container>
  );
}

ToppingInventoryTable.defaultProps = { list: [] };

ToppingInventoryTable.propTypes = { list: array };

export default ToppingInventoryTable;
