const initialState = {
  doughInventory: [],
  toppingInventory: [],
  usageList: {},
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_INVENTORY_STATS':
      return {
        ...state,
        doughInventory: action.data.doughUsed,
        toppingInventory: action.data.toppingsUsed,
      };
    default:
      return { ...state };
  }
};

export default reducer;
