import { array } from 'prop-types';
import { v4 } from 'uuid';

import { TableContainer as Container } from './styles';

function DoughInventoryTable({ list }) {
  return (
    <Container rows={parseInt(list.length, 10) + 1}>
      <div className='tableHeader'>
        <div>Size</div>
        <div>Calculated Weight</div>
        <div>Actual Weight</div>
        <div>Measurement</div>
        <div>Amount Spent</div>
        <div>Calculated Difference $</div>
        <div>Calculated Difference %</div>
      </div>
      {list?.map((dough) => (
        <div key={v4()}>
          <div>{dough.size}</div>
          <div>{dough.weight}</div>
          <div><input type="text" /></div>
          <div>{dough.measurement}</div>
          <div>${dough.amountSpent}</div>
          <div>$0</div>
          <div>0%</div>
        </div>
      ))}
    </Container>
  );
}

DoughInventoryTable.defaultProps = { list: [] };

DoughInventoryTable.propTypes = { list: array };

export default DoughInventoryTable;
